.menu-label {
    font-family: $family-sans-serif;
    font-weight: 700;
}

.menu-list {
    a {
        font-size: 1.1em;
        font-family: $family-sans-serif;
        font-weight: 400;
    }
}

.about-menu {
    border-right: 3px solid rgba($green, 0.3);
    // text-align: right;
    height: 100%;

    li ul {
        border-left: 0px solid rgba($green, 0.3);
        background: white;
        padding-left: 0;
        
        a { // sub menu links
            &:hover {
                background: rgba($blue, 0.3);
                // color: white;
            }

            &:before {
                font-family: FontAwesome;
                content: "\f054";
                display: inline-block;
                margin-right: 10px;
                color: $grey-light;
            }
        }
    }
}