.page-banner {
    margin-top: $navbar-height - 0.2;
    background-image: url("../images/barley.jpg");
    background-size: cover;
    background-repeat: no-repeat;

    > .container > .columns {
        min-height: 200px;
    }
}

.page-title {

    h1 {
    font-size: ms(2);
    font-weight: normal;
    }

    span {
        display: block;
    }

    .small {
        font-size: ms(-2);
    }
}

.page-content {
    min-height: 100vh;
}